import React from 'react';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import db from 'libs/db';
import AdminServices from 'services/api/AdminServices';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import { AiOutlineRight } from 'react-icons/ai';
import Steps from 'views/mobileView/components/atoms/Steps';


export default function RiskProfileIndex(props) {

	const [loading, setLoading] = React.useState(true);
	const stepsArray = [
		{
			title: 'Identifying your risk profile',
			description: 'We ask you a few questions and to identify your risk profile'
		},
		{
			title: 'Identifying your investment horizon',
			description: 'We check what is your ideal investment horizon. The longer you can invest, the better!'
		},
		{
			title: 'Designing your wealth creator',
			description: 'Taking your risk profile and investment horizon into account, we recommend you a wealth creator that is perfect for you!'
		}
	]
	React.useEffect(async () => {
		await db.get(db.model.clientData).then(async (p) => {
			if (p === null) {
				await db.get(db.model.user).then( async (user) => {
					let res = await AdminServices.getClient(user.userData.id).then(res => res);
					if (res.type === true) {
						const a = JSON.parse(res.data);
						let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
						console.log("Get Client API :", aa)
						await db.set(db.model.clientData, JSON.stringify(aa))
						if (aa.RiskInfo.length !== 0) {
							props.history.replace({pathname:'/risk-profile/result',state:{riskScore: aa.RiskInfo[0].RiskScore}})
						}
					}
				})
			} else if (p.RiskInfo.length !== 0) {
		        props.history.replace({pathname:'/risk-profile/result',state:{riskScore: p.RiskInfo[0].RiskScore}})

			} 
		})
		await db.remove(db.model.riskProfile)
		setLoading(false)
	}, [])

	return (
		<div className='mainContainer'>
			<MainHeader
				header="Letâ€™s build your personal portfolio"
				navigation={props.history}
			/>
			{
				loading ?
					<ActivityLoaderMintbox />
					:
					<>
						<div className="dataContainer">
							<Steps stepsArray={stepsArray} />
						</div>
						<div className='footer'>
							<button className='solidButton' onClick={() => props.history.push('/risk-profile/1')}>
								Get started <AiOutlineRight size={12} />
							</button>
						</div>
					</>
			}
		</div>
	)
}
